import React, { useEffect, useState } from 'react';
import './Greeting.css';
import { FcBriefcase,FcAlarmClock} from "react-icons/fc";

function Greetings(props){
    
    const[date,setDate]=useState(new Date());

    useEffect(()=>{
        setInterval(  
            () => setDate(new Date()),  
            3000  
          );  
    },[]);
    
    const stl_1={
        fontSize:"34px"
    }
    const stl_2={
        color:props.mode === "dark" ? "white":"rgb(177 172 172)"
    }
    return <div>
        <p className='h-1' style={stl_2}>
            <p>{`${date.toLocaleTimeString()}`}</p>
            <span className='clock-alarm'><FcAlarmClock/></span>
        </p>

        <p className='h-sub'>{`${props.closed} Completed`}</p>
        
        <p className='h-sub-2'>
            <p>{`${props.taskCount} Tasks to do`}</p> 
            <span style={stl_1} className="brief"><FcBriefcase/></span>
        </p>
    </div>
}

export default Greetings