import './ButtonT.css'
import { IoIosAdd } from "react-icons/io";

function ButtonT(props){

    const style={
        color:props.mode === "dark" ? "whitesmoke" : "#23262C",
        backgroundColor:props.mode === "dark" ? "#2E3239" : "white"
    }
    return(
        <button className='add' style={style} onClick={
            ()=>{
                
                props.onOpenModal();
            }
        }>
            <IoIosAdd/>
        </button>
    )
}

export default ButtonT