import './Task.css'
import { FcEmptyTrash } from "react-icons/fc";
import Badge from './Badge';
import { IoCloseOutline } from "react-icons/io5";

function Task(props){
    const style={
        border:`0.3px solid ${props.mode === "dark" ?'rgba(255,255,255,.1)':'rgba(156, 156, 156, 0.2)'}`
    }
    return(
        <div className="task" style={style}>
            <p className='todo'>
                <div className={props.mode === "dark" ? "txt-val" : "txt-val-lgt"}><p>{props.value}</p></div>
                <div className={props.mode === "dark" ? "close" : "close-lgt"} onClick={()=>{
                    props.onTaskDelete(props.id);
                }}><IoCloseOutline/></div>
            </p>
            <p className={props.mode === "dark"? "date":"date-lgt"}>{props.date}</p>
            <Badge letter={props.value[0]}/>
        </div>
    )
}

export default Task