import './ModeSwitcher.css'
import {WiMoonset,WiCloud} from 'react-icons/wi'

function ModeSwitcher(props){
    const style={
        color:props.mode === "dark" ? "whitesmoke" : "#23262C",
        backgroundColor:props.mode === "dark" ? "#2E3239" : "white"
    }
    return(
        <button className='switcher' style={style} onClick={()=>{
            if(props.mode === 'light'){props.changeMode('dark')}
            else{props.changeMode('light')}
          }}>
            {props.mode === 'dark' ? <WiCloud/> : <WiMoonset/>}
        </button>
    )
}

export default ModeSwitcher;