import React from 'react';
import './App.css';
import ButtonT from './components/ButtonT';
import Greetings from './components/Greeting';
import Modal from './components/Modal';
import ModeSwitcher from './components/ModeSwitcher';
import Task from './components/Task';

class App extends React.Component{

  constructor(props) {
    super(props);
    this.state={mode:'dark',todo:[],modal:false,closed:0};
    this.switchMode   = this.switchMode.bind(this);
    this.onTaskAdd    = this.onTaskAdd.bind(this);
    this.onOpenModal  = this.onOpenModal.bind(this);
    this.onModalClose = this.onModalClose.bind(this);
    this.onDeleteTask = this.onDeleteTask.bind(this);
  }

  switchMode(val){
    this.setState({mode:val});
    document.body.style.backgroundColor=this.state.mode === "dark" ? "whitesmoke" : "#23262C";
    document.body.style.transition="all .5s ease";
  }
  onOpenModal(){
    this.setState({modal:true});
  }
  onTaskAdd(obj){
    const oldState=this.state.todo;
    this.setState({todo:[...oldState,obj]});
  }
  onModalClose(){
    this.setState({modal:!this.state.modal});
  }
  componentDidMount(){
    document.body.style.backgroundColor=this.state.mode === "light" ? "whitesmoke" : "#23262C";
    document.body.style.transition="all .5s ease";
  }
  onDeleteTask(id){
    const oldState=this.state.todo;
    for(let i=0;i<oldState.length;i++){
      if(oldState[i].id === id){
        oldState.splice(i, 1);
      }
    }
    this.setState({closed:this.state.closed+1});
    this.setState({todo:oldState});
  }
  render(){
    return (
      <div className="app">

        {
          this.state.modal ?
          <div className='context-modal'>

          {this.state.modal === true?<Modal onModalClose={this.onModalClose} onAddTask={this.onTaskAdd}/>:<></>}
        
          </div>:<></>
        }
        

        <div className='topBar'>
            <ModeSwitcher mode={this.state.mode} changeMode={this.switchMode}/>
            <ButtonT mode={this.state.mode} onOpenModal={this.onOpenModal}/>
            <Greetings mode ={this.state.mode} closed={this.state.closed} taskCount={this.state.todo.length}/>
        </div>
        <div className='task-place'>
          
          {this.state.todo.length > 0?this.state.todo.map(
            (element)=>{
              return <Task 
              mode={this.state.mode}
              key={element.id} 
              value={element.value} 
              date={element.date} 
              id={element.id}
              onTaskDelete={this.onDeleteTask}/>
            }
          ):<p className={this.state.mode === "dark" ? "no-task":"no-task-lgt"}>No Ask added on todo 😇😎</p>}
        </div>
      </div>
    );
  }
}

export default App