import { useEffect, useState } from 'react'
import './Badge.css'

function Badge(props){

    const [color,setColor]=useState('#2ed573');
    const colors=['#2ed573','#706fd3','#ff5252','#ff793f','#ffd32a','#0fbcf9','#ef5777'];

    useEffect(()=>{
        const rd=Math.floor(Math.random() * colors.length);
        setColor(colors[rd]);
    },[]);
    
    return(
        <div className={props.mode === "dark" ? "Badge" : "Badge-lgt"}>
            <div className='circle' style={{backgroundColor:color}}>{props.letter}</div>
            <p>Task</p>
        </div>
    )
}

export default Badge