import "./Modal.css"
import { IoCloseOutline } from "react-icons/io5";
import React from "react";
import $ from 'jquery'

class Modal extends React.Component{
    constructor(props){
        super(props);
        this.state={task:''};
    }

    render(){
        return(
            <div className='modal'>
                <div className="container-modal">
                    
                    <div className="header-top">
                        <p>Add Task now 😂</p>
                        <div className='close-modal' onClick={()=>{
                            $(".modal").fadeOut("fast");
                            setTimeout(()=>{
                                this.props.onModalClose();
                            },200);
                        }}><IoCloseOutline/></div>
                    </div>
                    
                    <p>You can add task here</p>
                    <p>{new Date().toLocaleTimeString()}</p>
                    <input placeholder='Enter The Task' className='task_input' onChange={(e)=>{
                        this.setState({task:e.target.value});
                    }}/>

                    <button className="add-modal" onClick={()=>{
                        if(this.state.task.trim().length > 0){
                            let d=new Date().toLocaleTimeString();
                            this.props.onAddTask({id:Math.random(),value:this.state.task,date:d});
                            $(".modal").fadeOut("fast");
                            setTimeout(()=>{
                                this.props.onModalClose();
                            },200);
                        }else{
                            console.log("The input cannot empty");
                        }
                        this.setState({task:''});
                    }}>Add Task</button>
                </div>
            </div>
        );
    }
}

export default Modal